import React, { useState } from 'react';

const StockList = ({ stocks, indicatorValues }) => {
  const [filter, setFilter] = useState('');

  const handleFilterChange = (event) => {
    setFilter(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Handle form submission
  };

  return (
    <div>
      <h1>Stock List</h1>

      <form onSubmit={handleSubmit}>
        <select name="filter" value={filter} onChange={handleFilterChange}>
          <option value="">All Stocks</option>
          <option value="new_closing_highs">New Closing Highs</option>
          <option value="new_closing_lows">New Closing Lows</option>
          <option value="rsi_overbought">RSI Overbought</option>
          <option value="rsi_oversold">RSI Oversold</option>
          <option value="above_sma_20">Above SMA 20</option>
          <option value="below_sma_20">Below SMA 20</option>
          <option value="above_sma_50">Above SMA 50</option>
          <option value="below_sma_50">Below SMA 50</option>
        </select>
        <input type="submit"/>
      </form>

      <table className="ui padded selectable inverted table">
        <thead>
          <tr>
            <th>Symbol</th>
            <th>Name</th>
            <th>Price</th>
            <th>RSI 14</th>
            <th>SMA 20</th>
            <th>SMA 50</th>
          </tr>
        </thead>
        <tbody>
          {stocks.map((stock) => {
            const indicators = indicatorValues[stock.symbol];
            return (
              <tr key={stock.symbol}>
                <td>{stock.symbol}</td>
                <td><a href={`/stock/${stock.symbol}`}>{stock.name}</a></td>
                {indicators ? (
                  <>
                    <td>{indicators.close}</td>
                    <td>{indicators.rsi_14 ? indicators.rsi_14.toFixed(2) : 'NONE'}</td>
                    <td>{indicators.sma_20 ? indicators.sma_20.toFixed(2) : 'NONE'}</td>
                    <td>{indicators.sma_50 ? indicators.sma_50.toFixed(2) : 'NONE'}</td>
                  </>
                ) : (
                  <td colSpan={4}>No indicators</td>
                )}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default StockList;

ReactDOM.render(<StockList />, document.getElementById('root'));